@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;box-sizing:border-box;padding:0}body{background-color:#505050}.gol-container{position:absolute;width:auto;height:auto;left:50%;top:50%;transform:translate(-50%, -50%)}.title{position:relative;color:#fff;top:-1rem;left:.1rem;font-family:"Open Sans",sans-serif;font-size:1.5rem}.board{position:relative;width:328px;height:auto;max-height:544px;display:flex;flex-wrap:wrap;justify-content:center}.board .cell{position:relative;width:1rem;height:1rem;background-color:#acacac;margin-left:1px;margin-bottom:1px}.game-menu{position:relative;display:flex;justify-content:flex-end;height:2em;width:auto;margin-top:1em}.menu-btn{position:relative;display:flex;justify-content:space-evenly;align-items:center;border-radius:10px;font-weight:bold;color:#fff;background-color:#0d72f2;margin-left:3rem;font-family:"Open Sans",sans-serif}.menu-btn:hover{background-color:#0de4f2}.randomize{position:relative;width:10em;height:2em;cursor:pointer}.clear{position:relative;width:5em;height:2em;cursor:pointer}.togglePlay{position:relative;width:6em;height:2em;cursor:pointer}.alive{background-color:#0d72f2 !important}.newBorn{background-color:#0de4f2 !important}@media(min-width: 768px){.board{width:752px}}@media(min-width: 1200px){.board{width:1152px}}
